import getTime from 'date-fns/get_time';
import isWithinRange from 'date-fns/is_within_range';
import isDate from 'date-fns/is_date';
import get from 'lodash/get';
import addDays from 'date-fns/add_days';

export const dateFilter = (
  item,
  startDate = 0,
  endDate = Date.now(),
  timeStamp
) => {
  const rawTimeStamp = get(item, timeStamp);

  if (!rawTimeStamp) {
    console.warn(
      `order history item does not have a ${timeStamp} property:`,
      item
    );
    return false;
  }

  if (isDate(startDate) && isDate(endDate)) {
    return isWithinRange(
      rawTimeStamp,
      getTime(startDate),
      getTime(addDays(endDate, 1))
    );
  }

  return isWithinRange(rawTimeStamp, 0, Date.now());
};



