import React from 'react';
import PropTypes from 'prop-types';
import {
  formatPercentageValue,
  formatToThousands
} from '../../helpers/numberFormatter';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import APTable from '../common/APTable';

import './InstrumentSelectorPopupComponent.css';

const InstrumentSelectorPopupComponent = (props, context) => {
  const {
    onRowClick,
    level1,
    instruments,
    fetching,
    selectedRow,
    customClass,
    showFullNameInstrument
  } = props;
  const baseClasses = getBEMClasses('instrument-selector-popup', customClass);

  const columns = [
    {
      header: context.t('Coin'),
      classModifier: 'coin',
      cell: instrument =>
        showFullNameInstrument
          ? context.t(
              `${instrument.Product1FullName} / ${instrument.Product2FullName}`
            )
          : instrument.Symbol
    },
    {
      header: context.t('Price'),
      cell: instrument => instrument.lastTrade
    },
    {
      header: context.t('24hr Chg'),
      cell: instrument => instrument.Rolling24HrPxChange
    },
    {
      header: context.t('Volume'),
      cell: instrument => instrument.Rolling24HrVolume
    }
  ];

  const instrumentsRow = instruments.map(instrument => {
    const level1Data = level1[instrument.InstrumentId];
    return {
      ...instrument,
      lastTrade: level1Data && level1Data.LastTradedPx.toFixed(2),
      Rolling24HrPxChange: level1Data && (
        <span
          className={baseClasses(
            level1Data.Rolling24HrPxChange >= 0
              ? '24h-positive'
              : '24h-negative'
          )}>
          {(level1Data.Rolling24HrPxChange > 0 ? '+' : '') +
            formatPercentageValue(level1Data.Rolling24HrPxChange)}
        </span>
      ),
      Rolling24HrVolume:
        level1Data && formatToThousands(level1Data.Rolling24HrVolume)
    };
  });

  return (
    <APTable
      {...{
        fetching,
        columns,
        rows: instrumentsRow,
        baseClass: baseClasses,
        onRowClicked: instrument => onRowClick(instrument),
        selectedRow,
        empty: context.t('No coin is available')
      }}
    />
  );
};

InstrumentSelectorPopupComponent.propTypes = {
  level1: PropTypes.object.isRequired,
  onRowClick: PropTypes.func.isRequired,
  instruments: PropTypes.array.isRequired,
  fetching: PropTypes.bool.isRequired,
  selectedRow: PropTypes.object.isRequired
};

InstrumentSelectorPopupComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default InstrumentSelectorPopupComponent;



