import { createSelector } from 'reselect';
import { formValueSelector } from 'redux-form';
import { selectedInstrumentSelector } from './instrumentPositionSelectors';
import { dateFilter } from '../../helpers/orderHistoryFilters';

const formSelector = formValueSelector('order-history-filter');
const products = state => state.product.products;
const depositProductId = state => state.deposit.product;
const withdrawProductId = state => state.withdraw.product;

export const productsListSelector = createSelector(
  state => state.product.products,
  products =>
    products.map(({ Product, ProductFullName, ProductId, DecimalPlaces }) => ({
      Product,
      ProductFullName,
      ProductId,
      DecimalPlaces
    }))
);

export const filteredDepositsByProductSelector = createSelector(
  [
    selectedInstrumentSelector,
    state => state.deposit.depositsStatus,
    state => state.position.selectedProductId,
    state => formSelector(state, 'startDate', 'endDate'),
    (state, props) => props.filterMode
  ],
  (instrument, deposits, selectedProductId, formValues, filterMode) => {
    if (filterMode === 'selectedProduct') {
      return deposits
        .filter(deposit => deposit.AssetId === selectedProductId)
        .filter(deposit =>
          dateFilter(
            deposit,
            formValues.startDate,
            formValues.endDate,
            'CreatedTimestamp'
          )
        )
        .sort(
          (a, b) => new Date(b.CreatedTimestamp) - new Date(a.CreatedTimestamp)
        );
    } else {
      return deposits
        .filter(
          deposit =>
            deposit.AssetId === instrument.Product1 ||
            deposit.AssetId === instrument.Product2
        )
        .sort(
          (a, b) => new Date(b.CreatedTimestamp) - new Date(a.CreatedTimestamp)
        );
    }
  }
);

export const filteredWithdrawalsByProductSelector = createSelector(
  [
    selectedInstrumentSelector,
    state => state.withdraw.withdrawTickets,
    state => state.position.selectedProductId,
    state => formSelector(state, 'startDate', 'endDate'),
    (state, props) => props.filterMode
  ],
  (instrument, withdrawals, selectedProductId, formValues, filterMode) => {
    if (filterMode === 'selectedProduct') {
      return withdrawals
        .filter(withdrawal => withdrawal.AssetId === selectedProductId)
        .filter(withdrawal =>
          dateFilter(
            withdrawal,
            formValues.startDate,
            formValues.endDate,
            'CreatedTimestamp'
          )
        )
        .sort(
          (a, b) => new Date(b.CreatedTimestamp) - new Date(a.CreatedTimestamp)
        );
    } else {
      return withdrawals
        .filter(
          withdrawal =>
            withdrawal.AssetId === instrument.Product1 ||
            withdrawal.AssetId === instrument.Product2
        )
        .sort(
          (a, b) => new Date(b.CreatedTimestamp) - new Date(a.CreatedTimestamp)
        );
    }
  }
);

export const depositProductSelector = createSelector(
  [products, depositProductId],
  (products, depositProductId) =>
    products.find(prod => prod.ProductId === depositProductId)
);

export const withdrawProductSelector = createSelector(
  [products, withdrawProductId],
  (products, withdrawProductId) =>
    products.find(prod => prod.ProductId === withdrawProductId)
);



