import React from 'react';

import PaddedDecimal from '../common/PaddedDecimal/PaddedDecimal';
import { truncateToDecimals } from '../../helpers/numberFormatter';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import CancelAPButton from '../common/CancelAPButton';

const orderBookClasses = getBEMClasses('orderbook');
const flexTable = getBEMClasses('flex-table');

const BuyTableComponent = props => {
  const {
    cancelOrders,
    buyPriceClicked,
    buyQuantityClicked,
    decimalPlaces,
    level2: { buy }
  } = props;
  return buy.map((data, index) => (
    <div
      key={index}
      className={`${flexTable('row')} ${orderBookClasses('order-row', 'buy')}`}>
      <div
        onClick={() =>
          buyPriceClicked(
            truncateToDecimals(data.Price, decimalPlaces[data.Price.symbol])
          )
        }
        className={`${flexTable('column')} ${orderBookClasses(
          'table-price',
          'buy'
        )}`}>
        <PaddedDecimal
          bemMod="buy"
          value={data.Price}
          decimals={decimalPlaces[data.Price.symbol]}
        />
      </div>

      <div
        onClick={e =>
          buyQuantityClicked(
            truncateToDecimals(
              data.Quantity,
              decimalPlaces[data.Quantity.symbol]
            ),
            index,
            buy
          )
        }
        className={`${flexTable('column')} ${orderBookClasses(
          'table-qty',
          'buy'
        )}`}>
        <PaddedDecimal
          value={data.Quantity}
          decimals={decimalPlaces[data.Quantity.symbol]}
        />
      </div>

      <div
        className={`${flexTable('column')} ${orderBookClasses(
          'table-my-size'
        )}`}>
        {data.MySize && (
          <span>
            <PaddedDecimal value={data.MySize} decimals={5} />{' '}
            <CancelAPButton
              onClick={() => {
                if (data.OrderIds.length) {
                  cancelOrders(data.OrderIds);
                } else {
                  cancelOrders([data.OrderId]);
                }
              }}
              text=""
              customClass={orderBookClasses()}
            />
          </span>
        )}
      </div>
    </div>
  ));
};

export default BuyTableComponent;



