import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Tab from '../common/Tab/Tab';
import OpenOrdersComponent from './OpenOrdersComponent';
import FilledOrdersComponent from './FilledOrdersComponent';
import InactiveOrdersComponent from './InactiveOrdersComponent';
import OrderHistoryTradeReportsComponent from './OrderHistoryTradeReportsComponent';
import DepositsStatusComponent from './DepositsStatusComponent';
import WithdrawStatusComponent from './WithdrawStatusComponent';
import OrderHistoryHeaderWithFilterContainerForm from './OrderHistoryHeaderWithFilterContainer';

import './OrderHistoryComponent.css';
import './OrderHistoryTables.css';

export default class OrderHistoryComponent extends Component {
  static propTypes = {
    openOrders: PropTypes.array.isRequired,
    filledOrders: PropTypes.array.isRequired,
    inactiveOrders: PropTypes.array.isRequired,
    decimalPlacesById: PropTypes.object.isRequired,
    instruments: PropTypes.array.isRequired,
    cancelOrders: PropTypes.func.isRequired
  };

  _getPanes() {
    const { instruments, fetching, config, usePagination } = this.props;

    const localProps = {
      instruments,
      fetching,
      config,
      usePagination
    };

    return [
      {
        menuItem: OpenOrdersComponent.title,
        render: () => (
          <OpenOrdersComponent
            {...localProps}
            data={this.props.openOrders}
            cancelOrders={this.props.cancelOrders}
          />
        )
      },
      {
        menuItem: FilledOrdersComponent.title,
        render: () => (
          <FilledOrdersComponent
            {...localProps}
            data={this.props.filledOrders}
          />
        )
      },
      {
        menuItem: InactiveOrdersComponent.title,
        render: () => (
          <InactiveOrdersComponent
            {...localProps}
            data={this.props.inactiveOrders}
          />
        )
      },
      {
        menuItem: OrderHistoryTradeReportsComponent.title,
        render: () => (
          <OrderHistoryTradeReportsComponent
            {...localProps}
            data={this.props.tradeReports}
            cancelOrders={this.props.cancelOrders}
          />
        )
      },
      {
        menuItem: DepositsStatusComponent.title,
        render: () => (
          <DepositsStatusComponent
            {...localProps}
            data={this.props.depositsStatus}
            decimalPlaces={this.props.decimalPlacesById}
          />
        )
      },
      {
        menuItem: WithdrawStatusComponent.title,
        render: () => (
          <WithdrawStatusComponent
            {...localProps}
            data={this.props.withdrawTickets}
            decimalPlaces={this.props.decimalPlacesById}
            cancelWithdrawTicket={this.props.cancelWithdrawTicket}
          />
        )
      }
    ];
  }

  render() {
    return (
      <React.Fragment>
        {this.props.useHeaderWithFilter && (
          <OrderHistoryHeaderWithFilterContainerForm />
        )}
        <Tab
          panes={this._getPanes()}
          classModifiers={this.props.classModifiers}
          customClass="order-history"
        />
      </React.Fragment>
    );
  }
}



