import { createSelector } from 'reselect';
import { currency } from '../../helpers/numberFormatter';

export const recentTradesSelector = createSelector(
  [
    state => state.recentTrades,
    state => state.instrument.instruments,
    state => state.instrument.selectedInstrumentId
  ],
  (recentTrades, instruments, selectedInstrumentId) => {
    const tradesByInstrument = recentTrades[selectedInstrumentId];
    const instrument = instruments.find(
      item => item.InstrumentId === selectedInstrumentId
    );

    if (!instrument || !tradesByInstrument) {
      return [];
    }

    const { Product1Symbol, Product2Symbol } = instrument;

    return tradesByInstrument.map(trade => {
      const quantity = currency(trade.Quantity, Product1Symbol);
      const price = currency(trade.Price, Product2Symbol);

      return {
        ...trade,
        Quantity: quantity,
        Price: price
      };
    });
  }
);



