import React from 'react';
import PropTypes from 'prop-types';

import { getBEMClasses } from '../../helpers/cssClassesHelper';
import { formatDateTime } from '../../helpers/dateHelper';
import { formatNumberToLocale } from '../../helpers/numberFormatter';
import APTable from '../common/APTable';

const baseClass = getBEMClasses('order-history-table');

const FilledOrdersComponent = (
  { data, instruments, config, fetching, usePagination },
  context
) => {
  const { maxLines } = config;

  const columns = [
    {
      header: context.t('ID'),
      dataTest: 'ID',
      cell: row => row.TradeId
    },
    {
      header: context.t('Pair'),
      dataTest: 'Pair',
      cell: row => {
        const instrument = instruments.find(
          item => item.InstrumentId === row.InstrumentId
        );
        return instrument ? instrument.Symbol : '';
      }
    },
    {
      header: context.t('Side'),
      dataTest: 'Side',
      cell: row => (
        <div className={baseClass(row.Side === 'Buy' ? 'buy' : 'sell')}>
          {row.Side}
        </div>
      )
    },
    {
      header: context.t('Size'),
      dataTest: 'Size',
      cell: row => (
        <div className={baseClass('size')}>
          {row.Quantity &&
            formatNumberToLocale(row.Quantity, row.Quantity.symbol)}
        </div>
      )
    },
    {
      header: context.t('Price'),
      dataTest: 'Price',
      cell: row => {
        const price =
          row.OrderType === 'TrailingStopMarket' ||
          row.OrderType === 'TrailingStopLimit'
            ? 'TRL'
            : formatNumberToLocale(row.Price, row.Price.symbol);
        return <div className={baseClass('price')}>{row.Price && price}</div>;
      }
    },
    {
      header: context.t('Total'),
      dataTest: 'Total',
      cell: row => formatNumberToLocale(row.Value, row.Value.symbol)
    },
    {
      header: context.t('Fee'),
      dataTest: 'Fee',
      cell: row =>
        `${formatNumberToLocale(row.Fee, row.Fee.symbol)} ${row.Fee.symbol}`
    },
    {
      header: context.t('Execution ID'),
      dataTest: 'Execution ID',
      cell: row => row.ExecutionId
    },
    {
      header: context.t('Date/Time'),
      dataTest: 'Date/Time',
      cell: row => formatDateTime(new Date(row.TradeTimeMS))
    }
  ];

  return (
    <APTable
      {...{
        columns,
        rows: data,
        baseClass,
        fetching,
        pageSize: maxLines,
        usePagination,
        empty: context.t('No data is available'),
        componentName: 'FilledOrdersComponent'
      }}
    />
  );
};
/*
 this.context.t('Filled Orders');
 */
FilledOrdersComponent.title = 'Filled Orders';

FilledOrdersComponent.propTypes = {
  data: PropTypes.array.isRequired,
  instruments: PropTypes.array.isRequired,
  config: PropTypes.object.isRequired
};

FilledOrdersComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default FilledOrdersComponent;



