import { createSelector } from 'reselect';

export const decimalsByProductId = createSelector(
  state => state.product.products,
  products =>
    products.reduce((decimalPlaces, product) => {
      decimalPlaces[product.ProductId] = product.DecimalPlaces;
      return decimalPlaces;
    }, {})
);



