/* global TradingView */

import React from 'react';
import PropTypes from 'prop-types';
import ChartAPI from '../../services/tradingView/ChartAPI';
import jstz from 'jstimezonedetect';
import Spinner from '../common/Spinner/Spinner';
import path from '../../helpers/path';
import config from '../../config';
import { getGateway } from '../../helpers/wsHelper';
import { getCSSVar } from '../../helpers/cssVarHelper';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import './TVChartComponent.css';

const tvChartClasses = getBEMClasses(['tv-chart']);

class TVChartComponent extends React.Component {
  componentDidUpdate() {
    this.initChart();
  }

  shouldComponentUpdate(nextProps, nextState) {
    return !!window.TradingView && !!nextState && !!nextState.chartInitialized;
  }

  componentDidMount() {
    this.cssVariable = getCSSVar();
    this.scripts = {};

    this.script('/vendor/jquery-3.3.1.min.js')
      .then(() => {
        return this.script('/vendor/tradingView/charting_library.min.js');
      })
      .then(() => {
        this.initChart();
      });
  }

  initChart() {
    this.wsUri = getGateway();
    const timezone = jstz.determine().name();
    const currentTimezone = timezone ? timezone : config.TradingView.timezone;

    if (!this.chart) {
      if (!window.tvChartDataFeed) {
        window.tvChartDataFeed = new ChartAPI.UDFCompatibleDatafeed(
          this.wsUri,
          null,
          null,
          this.props.customGateway,
          currentTimezone
        );
      }

      this.createChart(window.tvChartDataFeed);
    } else {
      this.chart.setSymbol(this.props.selectedInstrument.Symbol, '15');
    }
  }

  script = location => {
    return new Promise(resolve => {
      this.scripts[location] = document.createElement('script');
      this.scripts[location].src = path(location);
      this.scripts[location].type = 'text/javascript';
      this.scripts[location].async = true;
      this.scripts[location].onload = resolve;
      document.body.appendChild(this.scripts[location]);
    });
  };

  componentWillUnmount() {
    clearTimeout(this.timeout);
    if (this.chart) {
      try {
        this.chart.remove();
      } catch (e) {
        console.warn('silently handled chart error: ', e);
      }
      this.scripts['/vendor/tradingView/charting_library.min.js'].remove();
      this.scripts['/vendor/jquery-3.3.1.min.js'].remove();
    }
  }

  createChart = dataFeed => {
    const sellColor = this.cssVariable('--exchange-colors__sell');
    const buyColor = this.cssVariable('--exchange-colors__buy');
    const volumeBuyColor = `rgba(${this.cssVariable(
      '--pc__accent-additive'
    )}, .3)`;
    const volumeSellColor = `rgba(${this.cssVariable(
      '--pc__accent-subtractive'
    )}, .3)`;
    const fontColorSecondary = this.cssVariable('--font__color-secondary');
    const separatorColor = this.cssVariable('--table__row-separator-color');
    const secondaryBgColor = this.cssVariable('--secondary__bg-color');

    const chartOptions = {
      symbol: this.props.selectedInstrument.Symbol,
      datafeed: dataFeed,
      library_path: path('/vendor/tradingView/'),
      autosize: true,
      interval: '15',
      container_id: 'trading-view-chart',
      loading_screen: { backgroundColor: secondaryBgColor },
      custom_css_url: path(
        '/vendor/tradingView/charting_library-dark-theme.css'
      ),
      overrides: {
        'paneProperties.background': secondaryBgColor,
        'paneProperties.gridProperties.color': separatorColor,
        'paneProperties.vertGridProperties.color': separatorColor,
        'paneProperties.horzGridProperties.color': separatorColor,
        'paneProperties.topMargin': 15,
        'paneProperties.bottomMargin': 25,
        'scalesProperties.textColor': this.cssVariable('--font__color-primary'),
        'scalesProperties.lineColor': fontColorSecondary,
        'scalesProperties.showLeftScale': false,
        'scalesProperties.showRightScale': true,
        'scalesProperties.backgroundColor': fontColorSecondary,
        volumePaneSize: 'large',
        'mainSeriesProperties.candleStyle.upColor': buyColor,
        'mainSeriesProperties.candleStyle.borderUpColor': buyColor,
        'mainSeriesProperties.candleStyle.downColor': secondaryBgColor,
        'mainSeriesProperties.candleStyle.borderDownColor': sellColor,
        'mainSeriesProperties.candleStyle.drawWick': true,
        'mainSeriesProperties.hollowCandleStyle.drawWick': false,
        'mainSeriesProperties.haStyle.drawWick': false,
        'mainSeriesProperties.lineStyle.linewidth': '4'
      },
      studies_overrides: {
        'volume.volume.color.0': volumeSellColor,
        'volume.volume.color.1': volumeBuyColor
      }
    };

    const localOverrides = config.TradingView || {};

    // For some reasons TradingView.onready is not fired, timeout is used as fallback to initialize chart after 1.5 sec
    this.timeout = setTimeout(() => {
      this.chart = new TradingView.widget({
        ...chartOptions,
        ...localOverrides
      });
      this.chart.onChartReady(() => {
        this.setState({ chartInitialized: true });
      });
    }, 1500);
  };

  render() {
    return (
      <div
        className={tvChartClasses('', {
          'with-depth-chart': config.TradingLayout.showDepthChart
        })}>
        <div className={tvChartClasses('header')}>
          {this.context.t('Price Chart')}
        </div>
        <div
          id="trading-view-chart"
          className={tvChartClasses('chart-container')}>
          <Spinner />
        </div>
      </div>
    );
  }
}

TVChartComponent.propTypes = {
  selectedInstrument: PropTypes.object
};

TVChartComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default TVChartComponent;



