/* global $ */
/*
 This class implements interaction with UDF-compatible datafeed.

 See UDF protocol reference at
 https://github.com/tradingview/charting_library/wiki/UDF
 */
import { APEX } from 'alphapoint';
import { getGateway } from '../../helpers/wsHelper';

const ChartAPI = {};
class UDFCompatibleDatafeed {
  constructor(
    datafeedURL,
    updateFrequency,
    protocolVersion,
    customGateway,
    currentTimezone
  ) {
    this._amsynchro = {};
    this._callbacks = {};
    this._configuration = undefined;
    this._datafeedURL = datafeedURL;
    this._enableLogging = false;
    this._initializationFinished = false;
    this._historyURL = '/history';
    this._protocolVersion = protocolVersion || 2;
    this._symbolSearch = null;
    this._symbolsStorage = null;
    this._symbolResolveURL = '/symbols';
    this._symbolRetries = 0;
    this._timezone = currentTimezone;

    this.callback = null;
    this.connected = false;
    this.lastBar = null;
    this.unsubscribeReqObj = {};
    this.gotBars = '';

    this.subscriptionIntervalMs = 0;
    const onopen = this._initialize.bind(this);
    this.ws = new APEX(customGateway ? customGateway : getGateway(), {
      onopen
    });
    this.ws.getInstruments(data => {
      this.instrumentsArray = JSON.parse(data.o);
    });
  }
  buildBar = barData => {
    const barValue = {
      time: barData[0],
      high: barData[1],
      low: barData[2],
      open: barData[3],
      close: barData[4],
      volume: barData[5]
    };
    return barValue;
  };
  getBars = (
    symbolInfo,
    resolution,
    rangeStartDate,
    rangeEndDate,
    onDataCallback
  ) => {
    const that = this;

    if (rangeStartDate > 0 && `${rangeStartDate}`.length > 10) {
      throw new Error(
        `Got a JS time instead of Unix one. rangeStartDate: ${rangeStartDate}, rangeEndDate: ${rangeEndDate}`
      );
    }

    let interval = 60;
    switch (resolution) {
      case '1':
        interval = 60;
        break;
      case '5':
        interval = 300;
        break;
      case '15':
        interval = 900;
        break;
      case '30':
        interval = 1800;
        break;
      case '60':
        interval = 3600;
        break;
      case '360':
        interval = 21600;
        break;
      case '720':
        interval = 43200;
        break;
      case 'D':
        interval = 86400;
        break;
      case 'W':
        interval = 604800;
        break;
      case 'M':
        interval = 2592000;
        break;
      default:
        interval = 60;
    }

    const newIntervalMs = interval * 1000;

    // Prevents looping for unknown reasons on certain symbols.
    if (
      this.gotBars === symbolInfo.name &&
      newIntervalMs === this.subscriptionIntervalMs
    ) {
      onDataCallback([], {
        version: this._protocolVersion,
        noData: true,
        nextTime: null
      });
    }

    const req = {};
    req.OMSId = symbolInfo.omsId;
    req.InstrumentId = symbolInfo.instrumentId;
    req.Interval = interval; // interval is seconds
    req.IncludeLastCount = 5000;
    this.subscriptionIntervalMs = newIntervalMs;
    that.ws.RPCCall('SubscribeTicker', req, (data = []) => {
      that.ws.RPCCall('TickerDataUpdateEvent', null, null, 2);
      data = JSON.parse(data.o);
      const noData = !data.length;
      const bars = [];
      const barsCount = data.length;

      for (let i = 0; i < barsCount; ++i) {
        const barData = data[i];
        const barValue = that.buildBar(barData);
        bars.push(barValue);
      }
      if (barsCount > 0) {
        this.gotBars = symbolInfo.name;
        this.lastBar = bars[bars.length - 1];
      } else {
        this.lastBar = null;
      }
      onDataCallback(bars, {
        version: that._protocolVersion,
        noData,
        nextTime: data.nb || data.nextTime || null
      });
    });
  };
  onMessage = ev => {
    const frame = JSON.parse(ev.data);
    const synchroCallBack = this._amsynchro[frame.i];
    if (synchroCallBack === undefined) {
      if (frame.n === 'TickerDataUpdateEvent') {
        const data = JSON.parse(frame.o);
        if (this.callback !== undefined && this.callback !== null) {
          const newBar = this.buildBar(data[0]);
          if (this.lastBar !== null) {
            if (newBar.time - this.subscriptionIntervalMs < this.lastBar.time) {
              newBar.time = this.lastBar.time;
            }
            this.callback(newBar);
            this.lastBar = newBar;
          }
        }
      }
    } else if (frame.o === '') {
      synchroCallBack({});
    } else if (synchroCallBack != null) {
      synchroCallBack(JSON.parse(frame.o));
    }
  };
  defaultConfiguration = () => ({
    supports_search: true,
    supports_group_request: false,
    supported_resolutions: [1, 5, 15, 30, 60, 360, 720, 'D', 'W', 'M'],
    supports_marks: false,
    supports_timescale_marks: false
  });
  getServerTime = callback => {
    if (this._configuration.supports_time) {
      this._send(`${this._datafeedURL}/time`, {})
        .done(response => {
          callback(+response);
        })
        .fail(() => {});
    }
  };
  on = (event, callback) => {
    if (!Object.prototype.hasOwnProperty.call(this._callbacks, event)) {
      this._callbacks[event] = [];
    }

    this._callbacks[event].push(callback);
    return this;
  };
  onInitialized = () => {
    this._initializationFinished = true;
    this._fireEvent('initialized');
  };
  onReady = callback => {
    const that = this;
    if (this._configuration) {
      setTimeout(() => {
        callback(that._configuration);
      }, 0);
    } else {
      this.on('configuration_ready', () => {
        callback(that._configuration);
      });
    }
  };
  // BEWARE: this function does not consider symbol's exchange
  resolveSymbol = (
    symbolName,
    onSymbolResolvedCallback,
    onResolveErrorCallback
  ) => {
    const symbol = {};
    const instrument = this.instrumentsArray.filter(
      i => i.Symbol === symbolName
    )[0];

    if (instrument) {
      symbol.omsId = instrument.OMSId;
      symbol.name = instrument.Symbol;
      symbol.full_name = instrument.Symbol;
      symbol.description = instrument.Symbol;
      symbol.instrumentId = instrument.InstrumentId;
      symbol.ticker = instrument.Symbol;
      symbol.minmov = 1;
      symbol.pricescale = 100;
      symbol.timezone = this._timezone;
      symbol.session = '24x7';
      symbol.has_intraday = true;

      setTimeout(() => {
        onSymbolResolvedCallback(symbol);
      }, 0);
    } else if (this._symbolRetries < 10) {
      this._symbolRetries++;
      console.log(`Symbol resolve retry #${this._symbolRetries}`);
      setTimeout(() => {
        this.resolveSymbol(
          symbolName,
          onSymbolResolvedCallback,
          onResolveErrorCallback
        );
      }, 100);
    } else {
      onResolveErrorCallback(
        `Could not resolve ${symbolName} after ${this._symbolRetries} retries`
      );
    }
  };
  searchSymbolsByName = () => {};
  searchSymbols = (searchString, exchange, type, onResultReadyCallback) => {
    const MAX_SEARCH_RESULTS = 30;
    if (!this._configuration) {
      onResultReadyCallback([]);
      return;
    }
    if (this._configuration.supports_search) {
      this._send(`${this._datafeedURL}/search`, {
        limit: MAX_SEARCH_RESULTS,
        query: searchString.toUpperCase(),
        type,
        exchange
      })
        .done(response => {
          const data = JSON.parse(response);

          for (let i = 0; i < data.length; ++i) {
            if (!data[i].params) {
              data[i].params = [];
            }
          }

          if (typeof data.s === 'undefined' || data.s !== 'error') {
            onResultReadyCallback(data);
          } else {
            onResultReadyCallback([]);
          }
        })
        .fail(reason => {
          onResultReadyCallback([]);
          throw new Error(reason);
        });
    } else {
      if (!this._symbolSearch) {
        throw new Error(
          'Datafeed error: inconsistent configuration (symbol search)'
        );
      }

      const searchArgument = {
        searchString,
        exchange,
        type,
        onResultReadyCallback
      };

      if (this._initializationFinished) {
        this._symbolSearch.searchSymbols(searchArgument, MAX_SEARCH_RESULTS);
      } else {
        const that = this;

        this.on('initialized', () => {
          that._symbolSearch.searchSymbols(searchArgument, MAX_SEARCH_RESULTS);
        });
      }
    }
  };
  subscribeBars = (symbolInfo, resolution, onRealtimeCallback) => {
    this.callback = onRealtimeCallback;
    this.unsubscribeReqObj.OMSId = symbolInfo.omsId;
    this.unsubscribeReqObj.InstrumentId = symbolInfo.instrumentId;
  };
  unsubscribeBars = () => {
    const req = this.unsubscribeReqObj;
    this.ws.RPCCall('UnsubscribeTicker', req);
  };
  _setupWithConfiguration = configurationData => {
    this._configuration = configurationData;

    if (!this._configuration.exchanges) {
      this._configuration.exchanges = [];
    }

    // @obsolete; remove in 1.5
    const supportedResolutions =
      this._configuration.supported_resolutions ||
      this._configuration.supportedResolutions;
    this._configuration.supported_resolutions = supportedResolutions;

    // @obsolete; remove in 1.5
    const symbolsTypes =
      this._configuration.symbols_types || this._configuration.symbolsTypes;
    this._configuration.symbols_types = symbolsTypes;

    if (
      !this._configuration.supports_search &&
      !this._configuration.supports_group_request
    ) {
      throw new Error(
        'Unsupported datafeed configuration. Must either support search, or support group request'
      );
    }
    if (!this._configuration.supports_search) {
      this._symbolSearch = new ChartAPI.SymbolSearchComponent(this);
    }
    if (this._configuration.supports_group_request) {
      // this component will call onInitialized() by itself
      // this._symbolsStorage = new ChartAPI.SymbolsStorage(this);
    } else {
      this.onInitialized();
    }
    this._fireEvent('configuration_ready');
    this._logMessage(`Initialized with ${JSON.stringify(configurationData)}`);
  };
  _initialize = () => {
    this._setupWithConfiguration(this.defaultConfiguration());

    this.ws.ws.filter(x => x.n === 'TickerDataUpdateEvent').subscribe(data => {
      data = data.o;
      if (this.callback !== null) {
        const newBar = this.buildBar(data[0]);

        if (
          this.lastBar !== null &&
          newBar &&
          typeof newBar.time !== 'string'
        ) {
          if (newBar.time - this.subscriptionIntervalMs < this.lastBar.time) {
            newBar.time = this.lastBar.time;
          }
          this.callback(newBar);
          this.lastBar = newBar;
        }
      }
    });
  };
  _fireEvent = (event, argument) => {
    if (Object.prototype.hasOwnProperty.call(this._callbacks, event)) {
      const callbacksChain = this._callbacks[event];
      for (let i = 0; i < callbacksChain.length; ++i) {
        callbacksChain[i](argument);
      }
      this._callbacks[event] = [];
    }
  };
  _logMessage = message => {
    if (this._enableLogging) {
      console.log(message, new Date());
    }
  };
  _send = (url, params) => {
    let request = url;
    if (params) {
      for (let i = 0; i < Object.keys(params).length; ++i) {
        const key = Object.keys(params)[i];
        const value = encodeURIComponent(params[key]);
        request += `${(i === 0 ? '?' : '&') + key}=${value}`;
      }
    }

    this._logMessage(`New request: ${request}`);

    return $.ajax({
      type: 'GET',
      url: request,
      contentType: 'text/plain'
    });
  };
}
ChartAPI.UDFCompatibleDatafeed = UDFCompatibleDatafeed;
export default ChartAPI;



