import { connect } from 'react-redux';
import OrderBookComponent from './OrderBookComponent';
import {
  cancelAllOrders,
  cancelBuyOrders,
  cancelSellOrders,
  cancelOrders
} from '../../redux/actions/orderHistoryActions';
import { formatNumberToLocale } from '../../helpers/numberFormatter';
import {
  buyQuantityClicked,
  buyPriceClicked,
  sellQuantityClicked,
  sellPriceClicked
} from '../../redux/actions/orderEntryActions';
import { level2Selector } from '../../redux/selectors/level2Selectors';
import { selectedInstrumentSelector } from '../../redux/selectors/instrumentPositionSelectors';
import {
  openOrdersSelectorFactory,
  buyOpenOrdersSelector,
  sellOpenOrdersSelector
} from '../../redux/selectors/orderHistorySelectors';

const makeMapStateToProps = () => {
  const openOrdersSelector = openOrdersSelectorFactory();

  return state => {
    const { level1, instrument } = state;
    const selectedInstrumentId = instrument.selectedInstrumentId;
    const level1Data = level1[selectedInstrumentId];
    const spread = level1Data ? level1Data.BestOffer - level1Data.BestBid : 0;
    const { fetching } = state.level2;
    const { decimalPlaces } = state.product;
    const filter = { filterMode: 'selectedInstrument' };
    const level2 = level2Selector(state, filter);
    const openOrders = openOrdersSelector(state, filter);
    const buyOpenOrders = buyOpenOrdersSelector(state, filter);
    const sellOpenOrders = sellOpenOrdersSelector(state, filter);
    const selectedInstrument = selectedInstrumentSelector(state);

    return {
      level2,
      spread: formatNumberToLocale(spread, selectedInstrument.Product2Symbol),
      decimalPlaces,
      fetching,
      selectedInstrumentId,
      openOrders,
      buyOpenOrders,
      sellOpenOrders
    };
  };
};

const mapDispatchToProps = {
  cancelAllOrders,
  cancelBuyOrders,
  cancelSellOrders,
  cancelOrders,
  buyQuantityClicked,
  buyPriceClicked,
  sellQuantityClicked,
  sellPriceClicked
};

export default connect(
  makeMapStateToProps,
  mapDispatchToProps
)(OrderBookComponent);



