import React from 'react';
import { connect } from 'react-redux';
import { reduxForm } from 'redux-form';
import OrderEntryComponent from './OrderEntryComponent';
import VerificationRequiredContainer from '../VerificationRequired/VerificationRequiredContainer';
import { placeOrder } from '../../redux/actions/orderEntryActions';
import { SIDE_PANES } from '../../constants/sidePanesConstants';
import { openSidePane } from '../../redux/actions/sidePaneActions';
import { marketPriceOfSelectedInstrumentSelector } from '../../redux/selectors/buySellSelectors';
import {
  buyValue,
  orderTypes
} from '../../constants/sendOrder/orderEntryFormConstants';
import { kycVerificationLevelsSelector } from '../../redux/selectors/kycLevelsSelectors';
import { selectedInstrumentSelector } from '../../redux/selectors/instrumentPositionSelectors';
import { orderFormTypes } from '../../constants/sendOrder/orderFormTypes';

const OrderEntryComponentForm = reduxForm({
  form: 'orderEntry',
  enableReinitialize: true,
  destroyOnUnmount: false,
  onSubmit: (payload, dispatch) =>
    dispatch(placeOrder(orderFormTypes.default, payload))
})(OrderEntryComponent);

const mapStateToProps = state => {
  const {
    form,
    instrument,
    position: { positions },
    user
  } = state;
  const { fetching } = instrument;
  const selectedInstrument = selectedInstrumentSelector(state);
  const selectedInstrumentId = selectedInstrument
    ? selectedInstrument.InstrumentId
    : 1;
  const { decimalPlaces } = state.product;
  const orderEntryForm = form.orderEntry || { values: {} };

  const { disableTrading } = kycVerificationLevelsSelector(state);

  return {
    authorizedUserPermissions: user.permissions,
    orderEntryForm,
    marketPrice: marketPriceOfSelectedInstrumentSelector(state, {
      side: orderEntryForm.values.side
    }),
    initialValues: {
      side: orderEntryForm.values.side || buyValue,
      orderType:
        orderEntryForm.values.orderType || orderTypes.limit.displayName,
      quantity: '',
      limitPrice: '',
      stopPrice: '',
      fee: '0',
      totalAmount: '0',
      selectedInstrumentId: selectedInstrumentId,
      timeInForce: '1'
    },
    selectedInstrument,
    decimalPlaces,
    positions,
    fetching,
    disableTrading,
    VerificationRequiredComponent: <VerificationRequiredContainer />
  };
};

const mapDispatchToProps = {
  openAdvancedOrderSidePane: () => openSidePane(SIDE_PANES.ADVANCED_ORDER),
  openReportBlockTradeSidePane: () =>
    openSidePane(SIDE_PANES.REPORT_BLOCK_TRADE)
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(OrderEntryComponentForm);



