import React from 'react';
import PropTypes from 'prop-types';
import APVerticalScroller from '../common/APVerticalScroller/APVerticalScroller';
import APInlineButton from '../common/APInlineButton/APInlineButton';
import BulkCancelButtonsComponent from './BulkCancelButtonsComponent/BulkCancelButtonsComponent';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import BuyTableComponent from './BuyTableComponent';
import SellTableComponent from './SellTableComponent';

import '../../styles/components/TradeComponent.css';
import './OrderBookComponent.css';

const tradeComponentClasses = getBEMClasses('trade-component');
const orderBookClasses = getBEMClasses('orderbook');
const flexTable = getBEMClasses('flex-table');

export default class OrderBookComponent extends React.Component {
  constructor(props, context) {
    super(props, context);
    // default state
    this.state = {
      showCenterButton: false //  will change to true when user scrolls away from rest position
    };
    // event handler bindings
    this.onClickCenter = this.onClickCenter.bind(this);
    this.onScrollRestChange = this.onScrollRestChange.bind(this);
    this.handleScrollRestChange = this.handleScrollRestChange.bind(this);
  }

  onClickCenter(overrideUserScrolled = false) {
    if (this.sellScroller && this.buyScroller) {
      const scrollToRest = () => {
        this.sellScroller.scrollToRest();
        this.buyScroller.scrollToRest();
      };

      if (overrideUserScrolled)
        return this.sellScroller.resetUserScrolled(() =>
          this.buyScroller.resetUserScrolled(scrollToRest)
        );
      scrollToRest();
    }
  }

  componentDidUpdate(prevProps) {
    if (this.props.selectedInstrumentId !== prevProps.selectedInstrumentId)
      this.onClickCenter(true);
    this.onClickCenter();
  }

  componentDidMount() {
    window.addEventListener('resize', () => this.onClickCenter(true));
    this.onClickCenter();
  }

  componentWillUnmount() {
    window.removeEventListener('resize', () => this.onClickCenter(true));
  }

  onScrollRestChange(value) {
    // show "center" button when user scrolls away from rest position
    setTimeout(this.handleScrollRestChange, 230);
  }

  handleScrollRestChange() {
    if (!!this.sellScroller) {
      const sellIsAtRest = this.sellScroller.getIsAtRest();
      const buyIsAtRest = this.buyScroller.getIsAtRest();
      const showCenterButton = !sellIsAtRest || !buyIsAtRest;
      this.setState({
        showCenterButton
      });
    }
  }

  render() {
    const {
      context,
      onScrollRestChange,
      props: {
        spread,
        cancelAllOrders,
        cancelBuyOrders,
        cancelSellOrders,
        openOrders,
        buyOpenOrders,
        sellOpenOrders
      }
    } = this;

    const CenterButton = ({ state, context, onClickCenter, spread }) => (
      <div className={orderBookClasses('spread-row')}>
        <span>
          {context.t('Spread')} {spread}
        </span>
        {state &&
          state.showCenterButton && (
            <APInlineButton
              icon="center"
              iconModifier="small"
              text={context.t('Center')}
              onClick={() => onClickCenter(true)}
              customClass={orderBookClasses('center-button')}
            />
          )}
      </div>
    );

    return (
      <div className={tradeComponentClasses()}>
        <div className={`${flexTable()} ${orderBookClasses()}`}>
          {this.props.showTitle && (
            <div className={flexTable('title')}>{context.t('Order Book')}</div>
          )}
          <div className={`${flexTable('body')} ${orderBookClasses('body')}`}>
            <div
              className={`${flexTable(
                'header',
                'background'
              )} ${orderBookClasses('header')}`}>
              <div className={flexTable('column')}>
                <div
                  className={`${flexTable('fixed')} ${orderBookClasses(
                    'table-header',
                    'price'
                  )}`}>
                  {context.t('Price')}
                </div>
              </div>
              <div className={flexTable('column')}>
                <div
                  className={`${flexTable('fixed')} ${orderBookClasses(
                    'table-header',
                    'qty'
                  )}`}>
                  {context.t('Qty')}
                </div>
              </div>
              <div className={flexTable('column')}>
                <div
                  className={`${flexTable('fixed')} ${orderBookClasses(
                    'table-header',
                    'my-size'
                  )}`}>
                  {context.t('My Size')}
                </div>
              </div>
            </div>
            <div className={orderBookClasses('order-book-body')}>
              <APVerticalScroller
                ref={el => (this.sellScroller = el)}
                restPosition="bottom"
                onScrollRestChange={onScrollRestChange}
                customClass={orderBookClasses('vertical-scroller-body')}>
                <div className="orderbook__table-filler">
                  <SellTableComponent {...this.props} />
                </div>
              </APVerticalScroller>

              {CenterButton({ ...{ ...this, context, spread } })}

              <APVerticalScroller
                ref={el => (this.buyScroller = el)}
                restPosition="top"
                onScrollRestChange={onScrollRestChange}
                customClass={orderBookClasses('vertical-scroller-body')}>
                <BuyTableComponent {...this.props} />
              </APVerticalScroller>
            </div>
          </div>
          <div>
            <BulkCancelButtonsComponent
              {...{
                openOrders,
                buyOpenOrders,
                sellOpenOrders,
                cancelAllOrders,
                cancelBuyOrders,
                cancelSellOrders
              }}
            />
          </div>
        </div>
      </div>
    );
  }
}

OrderBookComponent.defaultProps = {
  level2: {
    buy: [],
    sell: [],
    short: [],
    unknown: []
  },
  showTitle: true
};

OrderBookComponent.propTypes = {
  showTitle: PropTypes.bool
};

OrderBookComponent.contextTypes = {
  t: PropTypes.func.isRequired
};



