import { connect } from 'react-redux';
import {
  openOrdersSelectorFactory,
  filledOrdersSelectorFactory,
  inactiveOrdersSelectorFactory,
  tradeReportsSelectorFactory
} from '../../redux/selectors/orderHistorySelectors';
import {
  filteredDepositsByProductSelector,
  filteredWithdrawalsByProductSelector
} from '../../redux/selectors/depositWithdrawalSelectors';
import { decimalsByProductId } from '../../redux/selectors/productsSelectors';
import { cancelOrders } from '../../redux/actions/orderHistoryActions';
import { cancelWithdrawTicket } from '../../redux/actions/withdrawActions';
import OrderHistoryComponent from './OrderHistoryComponent';
import config from '../../config';

const mapDispatchToProps = {
  cancelOrders,
  cancelWithdrawTicket
};

const makeMapStateToProps = () => {
  const openOrdersSelector = openOrdersSelectorFactory();
  const filledOrdersSelector = filledOrdersSelectorFactory();
  const inactiveOrdersSelector = inactiveOrdersSelectorFactory();
  const tradeReportsSelector = tradeReportsSelectorFactory();

  return (state, ownProps) => {
    const openOrders = openOrdersSelector(state, ownProps);
    const filledOrders = filledOrdersSelector(state, ownProps);
    const inactiveOrders = inactiveOrdersSelector(state, ownProps);
    const tradeReports = tradeReportsSelector(state, ownProps);

    const { instruments } = state.instrument;
    const decimalPlacesById = decimalsByProductId(state);
    const depositsStatus = filteredDepositsByProductSelector(state, ownProps);
    const withdrawTickets = filteredWithdrawalsByProductSelector(
      state,
      ownProps
    );
    const { activeRequests } = state.orderHistory;
    const fetching = activeRequests > 0;
    const {
      TradingLayout,
      BalancesLayout,
      OrderHistoryData: { usePagination }
    } = config;
    const useBalancesConfig = ownProps.filterMode === 'selectedProduct';
    const { useHeaderWithFilter } = ownProps;

    return {
      openOrders,
      filledOrders,
      inactiveOrders,
      tradeReports,
      instruments,
      depositsStatus,
      withdrawTickets,
      decimalPlacesById,
      fetching,
      config: useBalancesConfig ? BalancesLayout : TradingLayout,
      usePagination,
      useHeaderWithFilter
    };
  };
};

export default connect(
  makeMapStateToProps,
  mapDispatchToProps
)(OrderHistoryComponent);



