import { connect } from 'react-redux';
import DepthChartComponent from './DepthChartComponent';
import store from '../../redux/store';
import {
  buyPriceClicked,
  sellPriceClicked
} from '../../redux/actions/orderEntryActions';

const mapStateToProps = state => {
  const { level2 } = state;
  const { fetching } = state.level2;
  const { selectedInstrumentId } = state.instrument;

  return {
    level2,
    store,
    fetching,
    selectedInstrumentId
  };
};

const mapDispatchToProps = {
  buyPriceClicked,
  sellPriceClicked
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(DepthChartComponent);



