import { createSelector } from 'reselect';
import { currency } from '../../helpers/numberFormatter';
import { mergeOpenOrders } from '../../helpers/orderHelper';
import {
  buyOpenOrdersSelector,
  sellOpenOrdersSelector
} from './orderHistorySelectors';
import { selectedInstrumentSelector } from './instrumentPositionSelectors';

export const convertLevel2SideDataToCurrency = () => (
  data,
  instrument,
  orders
) => {
  if (!instrument) {
    return data;
  }

  const { Product1Symbol, Product2Symbol } = instrument;

  const uniqOrders = mergeOpenOrders(orders);
  const userOrders = uniqOrders.map(order => {
    const quantity = currency(order.DisplayQuantity, Product1Symbol);

    return {
      ...order,
      Quantity: quantity,
      MySize: order.OrigQuantity
    };
  });

  const level2Orders = data.map(order => {
    const price = currency(order.Price, Product2Symbol);
    const quantity = currency(order.Quantity, Product1Symbol);
    const userOrderIndex = userOrders.findIndex(item =>
      item.Price.isEqualTo(price)
    );

    if (userOrderIndex !== -1) {
      const result = {
        ...userOrders[userOrderIndex],
        Quantity: quantity,
        MySize: userOrders[userOrderIndex].OrigQuantity
      };

      userOrders.splice(userOrderIndex, 1);
      return result;
    }

    return {
      ...order,
      Price: price,
      Quantity: quantity
    };
  });

  return level2Orders.sort((a, b) => b.Price.minus(a.Price));
};

export const level2BuySelector = createSelector(
  [
    state => state.level2.buy,
    selectedInstrumentSelector,
    buyOpenOrdersSelector
  ],
  convertLevel2SideDataToCurrency()
);

export const level2SellSelector = createSelector(
  [
    state => state.level2.sell,
    selectedInstrumentSelector,
    sellOpenOrdersSelector
  ],
  convertLevel2SideDataToCurrency()
);

export const level2Selector = createSelector(
  [level2BuySelector, level2SellSelector],
  (buy, sell) => {
    return {
      buy,
      sell
    };
  }
);



