import React from 'react';
import PropTypes from 'prop-types';
import { getParsedDecimal } from '../../../helpers/numberFormatter';

import './PaddedDecimal.css';

const PaddedDecimal = props => {
  const suffix = props.bemMod && props.bemMod.length ? '--' + props.bemMod : '';
  const parsed = getParsedDecimal(props.value, props.decimals);
  return (
    <span className={`ap-padded-decimal${suffix}`}>
      <span className={`ap-padded-decimal__units${suffix}`}>
        {parsed.units}
      </span>
      {parsed.decimals && (
        <React.Fragment>
          <span className={`ap-padded-decimal__decimal-separator${suffix}`}>
            {parsed.decimalSeparator}
          </span>
          <span className={`ap-padded-decimal__decimals${suffix}`}>
            {parsed.decimals}
          </span>
          <span className={`ap-padded-decimal__padding${suffix}`}>
            {parsed.padding}
          </span>
        </React.Fragment>
      )}
    </span>
  );
};

PaddedDecimal.propTypes = {
  value: PropTypes.any,
  decimals: PropTypes.number,
  bemMod: PropTypes.string
};

PaddedDecimal.defaultProps = {
  value: 0,
  decimals: 2,
  bemMod: null
};

export default PaddedDecimal;



