import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import config from '../../config';
import Spinner from '../common/Spinner/Spinner';
import { getCSSVar } from '../../helpers/cssVarHelper';

import 'apex-chart-react/lib/chart-style.css';
import '../../styles/components/TradeComponent.css';
import './DepthChart.css';

const tradeComponentClasses = getBEMClasses('trade-component');
const depthChartComponentClasses = getBEMClasses('depth-chart');

let mousePos = { x: 0, y: 0 };
let mouseOver = false;
const defaultDepthChartZoom = 5;

class DepthChartComponent extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      depthchartzoom: config.DepthChart.depthchartzoom || defaultDepthChartZoom
    };
  }

  componentDidMount() {
    this.cssVariable = getCSSVar();
    this.setListeners();
    this.drawChart();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.selectedInstrumentId !== this.props.selectedInstrumentId) {
      this.setState({
        depthchartzoom:
          config.DepthChart.depthchartzoom || defaultDepthChartZoom
      });
    }

    this.drawChart();
  }

  componentWillUnmount() {}

  setListeners = () => {
    const chartDom = document.getElementById('depthchart');
    chartDom.addEventListener('mouseover', () => {
      mouseOver = true;
    });
    chartDom.addEventListener('mouseout', () => {
      mouseOver = false;
      this.drawChart();
    });
    chartDom.addEventListener('mousemove', evt => {
      const rect = chartDom.getBoundingClientRect();
      mousePos = {
        x: evt.clientX - rect.left,
        y: evt.clientY - rect.top
      };

      this.drawChart();
    });
    chartDom.addEventListener('click', () => {
      if (this.mouseHoverBuy) {
        this.props.buyPriceClicked(this.closestMousePositionInfo.price);
      } else {
        this.props.sellPriceClicked(this.closestMousePositionInfo.price);
      }
    });
  };

  drawChart() {
    const { DepthChart } = config;
    const depthChartHeight = DepthChart.depthChartHeight || 300;
    const depthChartTextColor =
      DepthChart.depthChartTextColor || 'rgb(200,200,200)'; // Grey
    const depthChartTextFont = DepthChart.depthChartTextFont || '12px';
    const depthChartXAxisTextPadding =
      DepthChart.depthChartXAxisTextPadding || 60;
    const depthChartYAxisTextPadding =
      DepthChart.depthChartYAxisTextPadding || 20;
    const depthChartBottomPadding = DepthChart.depthChartBottomPadding || 20;
    const depthChartXAxisStepCnt = DepthChart.depthChartXAxisStepCnt || 9;
    const lastTradeDownColor = this.cssVariable('--chart__bearish-color');

    const lastTradeDownColorTransparent = this.cssVariable(
      '--chart__bearish-color_transparent'
    );

    const lastTradeUpColor = this.cssVariable('--chart__bullish-color');
    const lastTradeUpColorTransparent = this.cssVariable(
      '--chart__bullish-color_transparent'
    );

    const buyBook = this.props.level2.buy;
    const sellBook = this.props.level2.sell;

    const c2 = document.getElementById('depthchart');
    const ch = document.getElementById('depthChartHolder');
    c2.width = ch.clientWidth;
    c2.height = depthChartHeight;
    const chartHeight = c2.height;
    const chartWidth = c2.width;
    let maxBidPrice = 0;
    let minAskPrice = 999999;

    const sortedSellBook = sellBook.map(order => order.Price);
    sortedSellBook.forEach(price => {
      if (price < minAskPrice) minAskPrice = price;
    });

    const sortedBuyBook = buyBook.map(order => order.Price);
    sortedBuyBook.forEach(price => {
      if (price > maxBidPrice) maxBidPrice = price;
    });

    const center = (minAskPrice + maxBidPrice) / 2;

    this.relativeDepthChartZoom = Math.abs(
      (minAskPrice - maxBidPrice) * this.state.depthchartzoom
    );

    const midMarketPrice = document.getElementById('midMarketPrice');
    midMarketPrice.innerHTML = center.toFixed(2);
    const maxAskDisplay = center + this.relativeDepthChartZoom;
    const minBidDisplay = center - this.relativeDepthChartZoom;

    let visibleAskDepth = 0;
    let sellLevels = 0;
    for (let i = sortedSellBook.length - 1; i >= 0; --i) {
      if (sellBook[i].Price > maxAskDisplay) break;
      visibleAskDepth += sellBook[i].Quantity;
      ++sellLevels;
    }
    let visibleBidDepth = 0;
    let buyLevels = 0;
    for (let i = 0; i < sortedBuyBook.length; i++) {
      if (buyBook[i].Price < minBidDisplay) break;
      visibleBidDepth += buyBook[i].Quantity;
      ++buyLevels;
    }

    let topvisibleDepth = visibleAskDepth;
    if (visibleBidDepth > visibleAskDepth) topvisibleDepth = visibleBidDepth;

    const multX = chartWidth / (this.relativeDepthChartZoom * 2); // pixels per unit of price
    const multY = (chartHeight - depthChartBottomPadding) / topvisibleDepth; // pixels per unit of depth

    const ctx2 = c2.getContext('2d');
    ctx2.clearRect(0, 0, chartWidth, chartHeight);
    let lastY = chartHeight - depthChartBottomPadding;
    let lastX = chartWidth / 2;

    ctx2.beginPath();
    ctx2.moveTo(lastX, 50);
    ctx2.save();
    ctx2.lineWidth = 1;
    ctx2.lineTo(lastX, chartHeight - depthChartBottomPadding);
    ctx2.strokeStyle = depthChartTextColor;
    ctx2.stroke();

    this.mouseHoverBuy = false;
    this.mouseHoverSell = false;
    if (mousePos.x < lastX) {
      this.mouseHoverBuy = true;
    } else if (mousePos.x > lastX) {
      this.mouseHoverSell = true;
    }

    let lastTextEntry = lastX;
    let lastpx = center;
    lastX = chartWidth / 2 - (center - parseFloat(sortedBuyBook[0])) * multX;

    let lastXDrawn = 0;
    let lastYDrawn = 0;

    let lastTextEntryY = lastY;
    let lastTextEntryX = lastX;
    let depthShown = 0;

    this.closestMousePositionInfo = {
      price: 0,
      volume: 0,
      x: 0,
      y: 0,
      xdiff: 999999
    };
    this.closestMousePositionInfo.mouseX = mousePos.x;

    for (let i = 0; i < depthChartXAxisStepCnt; i++) {
      const xAxisText = (
        minBidDisplay +
        ((this.relativeDepthChartZoom * 2) / (depthChartXAxisStepCnt - 1)) * i
      ).toFixed(2);
      const xAxisStepI = ((chartWidth - 35) / (depthChartXAxisStepCnt - 1)) * i;
      ctx2.font = depthChartTextFont;
      ctx2.fillStyle = lastTradeUpColorTransparent;
      ctx2.fillText(xAxisText, xAxisStepI, chartHeight - 10);
    }

    for (let i = 0; i < buyLevels; i++) {
      const lvl = buyBook[i];
      depthShown += lvl.Quantity;
      const x = parseInt(lastX - (lastpx - lvl.Price) * multX, 10);
      const y =
        chartHeight -
        parseInt(depthShown * multY, 10) -
        depthChartBottomPadding;

      if (this.mouseHoverBuy) {
        if (
          this.closestMousePositionInfo.xdiff > Math.abs(lastX - mousePos.x)
        ) {
          this.closestMousePositionInfo.price = lvl.Price;
          this.closestMousePositionInfo.xdiff = Math.abs(lastX - mousePos.x);
          this.closestMousePositionInfo.volume = depthShown;
          this.closestMousePositionInfo.x = lastX;
          this.closestMousePositionInfo.y = y;
        }
      }

      lastpx = lvl.Price;

      if (lastTextEntryX - x > depthChartXAxisTextPadding && x - 40 > 0) {
        lastTextEntryX = x;
      }

      if (x <= chartWidth && y <= chartHeight && x >= 0) {
        ctx2.beginPath();
        ctx2.moveTo(lastX, y);
        ctx2.save();
        ctx2.lineWidth = 1;
        ctx2.lineTo(x, y);
        ctx2.strokeStyle = lastTradeUpColor;
        ctx2.stroke();

        ctx2.beginPath();
        ctx2.moveTo(lastX, lastY);
        ctx2.lineTo(lastX, y);
        ctx2.stroke();
        ctx2.restore();
        ctx2.fillStyle = lastTradeUpColorTransparent;

        ctx2.fillRect(
          lastX,
          y,
          x - lastX,
          chartHeight - depthChartBottomPadding - y
        );

        if (lastTextEntryY - y >= depthChartYAxisTextPadding && y - 10 > 0) {
          lastTextEntryY = y;
          ctx2.save();
          ctx2.beginPath();
          ctx2.moveTo(x, y);
          ctx2.lineW = 1;
          ctx2.lineTo(0, y);
          ctx2.strokeStyle = lastTradeUpColor;
          ctx2.setLineDash([2, 3]);
          ctx2.stroke();
          ctx2.restore();
        }

        if (x - lastTextEntry >= depthChartXAxisTextPadding) {
          ctx2.save();
          ctx2.beginPath();
          ctx2.moveTo(x, chartHeight - depthChartBottomPadding);
          ctx2.lineW = 1;
          ctx2.lineTo(x, chartHeight - 10);
          ctx2.strokeStyle = lastTradeUpColor;
          ctx2.stroke();
          ctx2.restore();
          lastTextEntry = x;
        }

        lastXDrawn = x;
        lastYDrawn = y;
      }

      lastX = x;
      lastY = y;
    }

    ctx2.fillStyle = lastTradeUpColorTransparent;
    ctx2.fillRect(
      0,
      lastYDrawn,
      lastXDrawn,
      chartHeight - depthChartBottomPadding - lastYDrawn
    );
    ctx2.beginPath();
    ctx2.moveTo(lastXDrawn, lastYDrawn);
    ctx2.save();
    ctx2.lineWidth = 1;
    ctx2.lineTo(0, lastYDrawn);
    ctx2.strokeStyle = lastTradeUpColor;
    ctx2.stroke();
    ctx2.restore();

    lastY = chartHeight - depthChartBottomPadding;
    lastX = chartWidth / 2;
    lastTextEntry = lastX;
    lastpx = center;
    lastTextEntryY = lastY;
    lastX =
      chartWidth / 2 +
      (parseFloat(sortedSellBook[sortedSellBook.length - 1]) - center) * multX;
    lastTextEntryX = 0;
    let shownum = sortedSellBook.length - sellLevels - 1;
    if (shownum < 0) shownum = 0;
    let depthShownA = 0;
    for (let i = sortedSellBook.length - 1; i >= shownum; i--) {
      const lvl = sellBook[i];
      depthShownA += lvl.Quantity;
      const x = parseInt(lastX + (lvl.Price - lastpx) * multX, 10);
      const y =
        chartHeight -
        parseInt(depthShownA * multY, 10) -
        depthChartBottomPadding;
      lastpx = lvl.Price;

      if (this.mouseHoverSell) {
        if (
          this.closestMousePositionInfo.xdiff > Math.abs(lastX - mousePos.x)
        ) {
          this.closestMousePositionInfo.xdiff = Math.abs(lastX - mousePos.x);
          this.closestMousePositionInfo.price = lvl.Price;
          this.closestMousePositionInfo.volume = depthShownA;
          this.closestMousePositionInfo.x = lastX;
          this.closestMousePositionInfo.y = y;
        }
      }

      if (
        x - lastTextEntryX > depthChartXAxisTextPadding &&
        x + 40 < chartWidth
      ) {
        lastTextEntryX = x;
      }

      if (x <= chartWidth && y <= chartHeight && x >= 0) {
        ctx2.save();
        ctx2.beginPath();
        ctx2.moveTo(lastX, y);
        ctx2.lineWidth = 1;
        ctx2.lineTo(x, y);
        ctx2.strokeStyle = lastTradeDownColor;
        ctx2.stroke();

        ctx2.beginPath();
        ctx2.moveTo(lastX, lastY);
        ctx2.lineTo(lastX, y);
        ctx2.stroke();
        ctx2.restore();
        ctx2.fillStyle = lastTradeDownColorTransparent;

        ctx2.fillRect(
          lastX,
          y,
          x - lastX,
          chartHeight - depthChartBottomPadding - y
        );

        if (lastTextEntryY - y >= depthChartYAxisTextPadding && y - 10 > 0) {
          lastTextEntryY = y;
          ctx2.save();
          ctx2.beginPath();
          ctx2.moveTo(chartWidth, y);
          ctx2.lineW = 1;
          ctx2.lineTo(x, y);
          ctx2.strokeStyle = lastTradeDownColor;
          ctx2.setLineDash([2, 3]);
          ctx2.stroke();
          ctx2.restore();
        }
        if (lastTextEntry - x >= depthChartXAxisTextPadding) {
          ctx2.font = depthChartTextFont;
          ctx2.fillStyle = depthChartTextColor;
          ctx2.fillText(lvl.Price.toFixed(2), x, chartHeight);

          ctx2.save();
          ctx2.beginPath();
          ctx2.moveTo(x, chartHeight - depthChartBottomPadding);
          ctx2.lineW = 1;
          ctx2.lineTo(x, chartHeight - 10);
          ctx2.strokeStyle = lastTradeDownColor;
          ctx2.stroke();
          ctx2.restore();

          lastTextEntry = x;
        }
        lastXDrawn = x;
        lastYDrawn = y;
      }
      lastX = x;
      lastY = y;
    }

    ctx2.fillStyle = lastTradeDownColorTransparent;
    ctx2.fillRect(
      lastXDrawn,
      lastYDrawn,
      chartWidth,
      chartHeight - depthChartBottomPadding - lastYDrawn
    );
    ctx2.beginPath();
    ctx2.moveTo(lastXDrawn, lastYDrawn);
    ctx2.save();
    ctx2.lineWidth = 1;
    ctx2.lineTo(chartWidth, lastYDrawn);
    ctx2.strokeStyle = lastTradeDownColor;
    ctx2.stroke();
    ctx2.restore();

    if (mouseOver && (this.mouseHoverBuy || this.mouseHoverSell)) {
      // ctx2.save();
      ctx2.beginPath();
      // ctx2.moveTo(this.closestMousePositionInfo.x, this.closestMousePositionInfo.y);
      ctx2.arc(
        this.closestMousePositionInfo.x,
        this.closestMousePositionInfo.y,
        3,
        0,
        2 * Math.PI,
        false
      );
      ctx2.fillStyle = 'rgba(205,205,205, 0.3)';
      ctx2.fill();
      ctx2.lineWidth = 1;
      ctx2.strokeStyle = 'rgba(205,205,205, 0.3)';
      ctx2.stroke();
      // ctx2.restore();
      if (this.mouseHoverBuy) {
        ctx2.font = depthChartTextFont;
        ctx2.fillStyle = depthChartTextColor;
        ctx2.fillText(
          this.closestMousePositionInfo.price.toFixed(2),
          this.closestMousePositionInfo.x + 20,
          this.closestMousePositionInfo.y - 40
        );
        ctx2.fillText(
          'Buy',
          this.closestMousePositionInfo.x + 20,
          this.closestMousePositionInfo.y - 25
        );
        ctx2.fillText(
          this.closestMousePositionInfo.volume.toFixed(2),
          this.closestMousePositionInfo.x + 50,
          this.closestMousePositionInfo.y - 25
        );
        ctx2.fillText(
          'Cost',
          this.closestMousePositionInfo.x + 20,
          this.closestMousePositionInfo.y - 10
        );
        ctx2.fillText(
          (
            this.closestMousePositionInfo.volume *
            this.closestMousePositionInfo.price
          ).toFixed(2),
          this.closestMousePositionInfo.x + 50,
          this.closestMousePositionInfo.y - 10
        );
        ctx2.fillStyle = lastTradeUpColorTransparent;
        ctx2.fillRect(
          this.closestMousePositionInfo.x,
          this.closestMousePositionInfo.y - 60,
          100,
          60
        );
      }
      if (this.mouseHoverSell) {
        ctx2.font = depthChartTextFont;
        ctx2.fillStyle = depthChartTextColor;
        ctx2.fillText(
          this.closestMousePositionInfo.price.toFixed(2),
          this.closestMousePositionInfo.x - 90,
          this.closestMousePositionInfo.y - 40
        );
        ctx2.fillText(
          'Buy',
          this.closestMousePositionInfo.x - 90,
          this.closestMousePositionInfo.y - 25
        );
        ctx2.fillText(
          this.closestMousePositionInfo.volume.toFixed(2),
          this.closestMousePositionInfo.x - 60,
          this.closestMousePositionInfo.y - 25
        );
        ctx2.fillText(
          'Cost',
          this.closestMousePositionInfo.x - 90,
          this.closestMousePositionInfo.y - 10
        );
        ctx2.fillText(
          (
            this.closestMousePositionInfo.volume *
            this.closestMousePositionInfo.price
          ).toFixed(2),
          this.closestMousePositionInfo.x - 60,
          this.closestMousePositionInfo.y - 10
        );
        ctx2.fillStyle = lastTradeUpColorTransparent;
        ctx2.fillRect(
          this.closestMousePositionInfo.x,
          this.closestMousePositionInfo.y - 60,
          -100,
          60
        );
      }
    }
  }

  zoomDpethChartPlus = () => {
    const newDepthChartZoom = this.state.depthchartzoom / 1.25;
    if (newDepthChartZoom < 0.0001) {
      this.setState({
        depthchartzoom: 0.0001
      });
    } else {
      this.setState({
        depthchartzoom: newDepthChartZoom
      });
    }

    this.drawChart();
  };

  zoomDpethChartMinus = () => {
    const newDepthChartZoom = this.state.depthchartzoom * 1.25;
    if (newDepthChartZoom > 1000) {
      this.setState({
        depthchartzoom: 1000
      });
    } else {
      this.setState({
        depthchartzoom: newDepthChartZoom
      });
    }

    this.drawChart();
  };

  render() {
    const chartContainerHeight = config.DepthChart.depthChartHeight
      ? config.DepthChart.depthChartHeight + 50
      : 350;

    return (
      <div
        style={{
          minHeight: chartContainerHeight,
          height: chartContainerHeight
        }}
        className={`${tradeComponentClasses()} ${depthChartComponentClasses()}`}>
        <div className={`${tradeComponentClasses('header')}`}>
          {this.context.t('Depth Chart')}
        </div>
        {this.props.fetching && <Spinner />}
        <div
          id="depthChartHolder"
          className={`${depthChartComponentClasses('')}`}>
          <canvas id="depthchart" style={{ position: 'absolute', zIndex: 2 }} />
          <div className={`${depthChartComponentClasses('buttons')}`}>
            <button
              className={`${depthChartComponentClasses('button--zoom')}`}
              onClick={() => this.zoomDpethChartPlus()}>
              +
            </button>
            <span id="midMarketPrice">0</span>
            <button
              className={`${depthChartComponentClasses('button--zoom')}`}
              onClick={() => this.zoomDpethChartMinus()}>
              -
            </button>
          </div>
          <div className={`${depthChartComponentClasses('zoom-label')}`}>
            {this.context.t(`Zoom: ${this.state.depthchartzoom.toFixed(2)}`)}
          </div>
          <div className={`${depthChartComponentClasses('label')}`}>
            {this.context.t('Mid Market Price')}
          </div>
        </div>
      </div>
    );
  }
}

DepthChartComponent.propTypes = {
  store: PropTypes.object.isRequired,
  level2: PropTypes.object.isRequired
};

DepthChartComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default DepthChartComponent;



