import React from 'react';

import PaddedDecimal from '../common/PaddedDecimal/PaddedDecimal';
import { truncateToDecimals } from '../../helpers/numberFormatter';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import CancelAPButton from '../common/CancelAPButton';
import Spinner from '../common/Spinner/Spinner';

const orderBookClasses = getBEMClasses('orderbook');
const flexTable = getBEMClasses('flex-table');

const SellTableComponent = props => {
  const {
    cancelOrders,
    sellPriceClicked,
    sellQuantityClicked,
    decimalPlaces,
    level2: { sell },
    fetching
  } = props;

  if (fetching) {
    return <Spinner />;
  } else {
    return sell.map((data, index) => (
      <div
        key={index}
        className={`${flexTable('row')} ${orderBookClasses(
          'order-row',
          'sell'
        )}`}>
        <div
          onClick={() =>
            sellPriceClicked(
              truncateToDecimals(data.Price, decimalPlaces[data.Price.symbol])
            )
          }
          className={`${flexTable('column')} ${orderBookClasses(
            'table-price',
            'sell'
          )}`}>
          <PaddedDecimal
            bemMod="sell"
            value={data.Price}
            decimals={decimalPlaces[data.Price.symbol]}
          />
        </div>

        <div
          onClick={e =>
            sellQuantityClicked(
              truncateToDecimals(
                data.Quantity,
                decimalPlaces[data.Quantity.symbol]
              ),
              index,
              sell
            )
          }
          className={`${flexTable('column')} ${orderBookClasses(
            'table-qty',
            'sell'
          )}`}>
          <PaddedDecimal
            value={data.Quantity}
            decimals={decimalPlaces[data.Quantity.symbol]}
          />
        </div>

        <div
          className={`${flexTable('column')} ${orderBookClasses(
            'table-my-size'
          )}`}>
          {data.MySize && (
            <span>
              <PaddedDecimal
                value={data.MySize}
                decimals={decimalPlaces[data.Quantity.symbol]}
              />{' '}
              <CancelAPButton
                onClick={e => {
                  if (data.OrderIds.length) {
                    cancelOrders(data.OrderIds);
                  } else {
                    cancelOrders([data.OrderId]);
                  }
                }}
                text=""
                customClass={orderBookClasses()}
              />
            </span>
          )}
        </div>
      </div>
    ));
  }
};

export default SellTableComponent;



