import React from 'react';
import PropTypes from 'prop-types';
import APTable from '../common/APTable';
import CancelAPButton from '../common/CancelAPButton';

import { formatDateTime } from '../../helpers/dateHelper';
import { formatNumberToLocale } from '../../helpers/numberFormatter';
import { getBEMClasses } from '../../helpers/cssClassesHelper';

const baseClass = getBEMClasses('order-history-table');

const OpenOrdersComponent = (
  { data, instruments, cancelOrders, fetching, config, usePagination },
  context
) => {
  const { maxLines } = config;

  const columns = [
    {
      header: context.t('Pair'),
      dataTest: 'Pair',
      cell: row => {
        const instrument = instruments.find(
          item => item.InstrumentId === row.Instrument
        );
        return instrument ? instrument.Symbol : '';
      }
    },
    {
      header: context.t('Side'),
      dataTest: 'Side',
      cell: row => (
        <div className={baseClass(row.Side === 'Buy' ? 'buy' : 'sell')}>
          {row.Side}
        </div>
      )
    },
    {
      header: context.t('Type'),
      dataTest: 'Type',
      cell: row => (row.IsQuote ? context.t('Quote') : row.OrderType)
    },
    {
      header: context.t('Size'),
      dataTest: 'Size',
      cell: row => (
        <div className={baseClass('size')}>
          {row.quantity &&
            formatNumberToLocale(row.quantity, row.quantity.symbol)}
        </div>
      )
    },
    {
      header: context.t('Price'),
      dataTest: 'Price',
      cell: row => (
        <div className={baseClass('price')}>
          {row.Price && formatNumberToLocale(row.Price, row.Price.symbol)}
        </div>
      )
    },
    {
      header: context.t('Date/Time'),
      dataTest: 'Date/Time',
      cell: row => formatDateTime(row.ReceiveTime)
    },
    {
      header: context.t('Status'),
      dataTest: 'Status',
      cell: row => row.OrderState
    },
    {
      header: context.t('Action'),
      dataTest: 'Action',
      cell: row => (
        <CancelAPButton
          text={context.t('Cancel')}
          dataTest="Cancel"
          onClick={() => {
            cancelOrders([row.OrderId]);
          }}
        />
      )
    }
  ];

  const rowProps = row => ({
    'data-tip': true,
    'data-for': `orderInfo-${row.OrderId}`
  });

  return (
    <APTable
      {...{
        columns,
        rows: data,
        baseClass,
        fetching,
        rowProps,
        pageSize: maxLines,
        usePagination,
        empty: context.t('No data is available'),
        componentName: 'OpenOrdersComponent'
      }}
    />
  );
};

/*
 this.context.t('Open Orders');
 */
OpenOrdersComponent.title = 'Open Orders';

OpenOrdersComponent.propTypes = {
  data: PropTypes.array.isRequired,
  instruments: PropTypes.array.isRequired,
  cancelOrders: PropTypes.func.isRequired
};

OpenOrdersComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default OpenOrdersComponent;



