import { createSelector } from 'reselect';
import { currency } from '../../helpers/numberFormatter';
import { dateFilter } from '../../helpers/orderHistoryFilters';
import { formValueSelector } from 'redux-form';

const formSelector = formValueSelector('order-history-filter');

const openOrdersCurrencySelector = createSelector(
  [
    state => state.orderHistory.openOrders,
    state => state.instrument.instruments,
    state => state.orderHistory.tradeReports
  ],
  convertOrderNumbersToCurrency
);

export const openOrdersSelector = createSelector(
  openOrdersCurrencySelector,
  orders => {
    return orders
      .filter(
        order =>
          order.OrderState !== 'FullyExecuted' && order.OrderState === 'Working'
      )
      .sort((a, b) => b.ReceiveTime - a.ReceiveTime);
  }
);

export const openOrdersSelectorFactory = () =>
  createSelector(
    [
      openOrdersSelector,
      state => state.position.selectedProductId,
      state => state.instrument.selectedInstrumentId,
      state => state.instrument.instruments,
      state => formSelector(state, 'startDate', 'endDate'),
      (state, props) => props.filterMode
    ],
    (
      orders,
      selectedProductId,
      selectedInstrumentId,
      instruments,
      formValues,
      filterMode
    ) => {
      if (filterMode === 'selectedProduct') {
        const filteredOrders = filterOrdersByProductId(
          orders,
          selectedProductId,
          instruments
        );
        return filterOrdersByDate(filteredOrders, formValues, 'ReceiveTime');
      } else {
        // else filterMode == 'selectedInstrument'
        return orders.filter(
          order => order.Instrument === selectedInstrumentId
        );
      }
    }
  );

export const buyOpenOrdersSelector = createSelector(
  openOrdersSelectorFactory(),
  orders => {
    return orders.filter(
      order => order.Side === 'Buy' && order.OrderType !== 'BlockTrade'
    );
  }
);

export const sellOpenOrdersSelector = createSelector(
  openOrdersSelectorFactory(),
  orders => {
    return orders.filter(
      order => order.Side === 'Sell' && order.OrderType !== 'BlockTrade'
    );
  }
);

const accountTradesCurrencySelector = createSelector(
  [
    state => state.orderHistory.accountTrades,
    state => state.instrument.instruments,
    state => state.product.products
  ],
  (trades, instruments, products) => {
    return trades.map(trade => {
      const instrument = instruments.find(
        item => item.InstrumentId === trade.InstrumentId
      );
      if (!instrument) return {};
      const { Product1Symbol, Product2Symbol } = instrument;
      const quantity = currency(trade.Quantity, Product1Symbol);
      const price = currency(trade.Price, Product2Symbol);
      const symbol = trade.Side === 'Buy' ? Product1Symbol : Product2Symbol;
      const value = currency(trade.Value, symbol);
      const feeProduct = products.find(p => p.ProductId === trade.FeeProductId);
      const fee = currency(trade.Fee, feeProduct ? feeProduct.Product : symbol);

      return {
        ...trade,
        Quantity: quantity,
        Price: price,
        Value: value,
        Fee: fee
      };
    });
  }
);

const filledOrdersSelector = createSelector(
  accountTradesCurrencySelector,
  trades =>
    trades.filter(trade => !trade.IsBlockTrade).sort((a, b) => {
      if (a.TradeTime < b.TradeTime) return 1;
      if (a.TradeTime > b.TradeTime) return -1;
      return 0;
    })
);

export const filledOrdersSelectorFactory = () =>
  createSelector(
    [
      filledOrdersSelector,
      state => state.position.selectedProductId,
      state => state.instrument.selectedInstrumentId,
      state => state.instrument.instruments,
      state => formSelector(state, 'startDate', 'endDate'),
      (state, props) => props.filterMode
    ],
    (
      orders,
      selectedProductId,
      selectedInstrumentId,
      instruments,
      formValues,
      filterMode
    ) => {
      if (filterMode === 'selectedProduct') {
        const filteredOrders = filterOrdersByProductId(
          orders,
          selectedProductId,
          instruments
        );
        return filterOrdersByDate(filteredOrders, formValues, 'TradeTimeMS');
      } else {
        // else filterMode == 'selectedInstrument'
        return orders.filter(
          order => order.InstrumentId === selectedInstrumentId
        );
      }
    }
  );

const inactiveOrdersCurrencySelector = createSelector(
  [
    state => state.orderHistory.inactiveOrders,
    state => state.instrument.instruments
  ],
  convertOrderNumbersToCurrency
);

const inactiveOrdersSelector = createSelector(
  inactiveOrdersCurrencySelector,
  orders => {
    return orders
      .filter(
        order =>
          order.OrderState !== 'FullyExecuted' && order.OrderState !== 'Working'
      )
      .sort((a, b) => b.ReceiveTime - a.ReceiveTime);
  }
);

export const inactiveOrdersSelectorFactory = () =>
  createSelector(
    [
      inactiveOrdersSelector,
      state => state.position.selectedProductId,
      state => state.instrument.selectedInstrumentId,
      state => state.instrument.instruments,
      state => formSelector(state, 'startDate', 'endDate'),
      (state, props) => props.filterMode
    ],
    (
      orders,
      selectedProductId,
      selectedInstrumentId,
      instruments,
      formValues,
      filterMode
    ) => {
      if (filterMode === 'selectedProduct') {
        const filteredOrders = filterOrdersByProductId(
          orders,
          selectedProductId,
          instruments
        );
        return filterOrdersByDate(filteredOrders, formValues, 'ReceiveTime');
      } else {
        // else filterMode == 'selectedInstrument'
        return orders.filter(
          order => order.Instrument === selectedInstrumentId
        );
      }
    }
  );

function convertOrderNumbersToCurrency(orders, instruments, tradeReports = []) {
  const allOrders = [...orders, ...tradeReports];

  return allOrders.map(order => {
    const instrument = instruments.find(
      item => item.InstrumentId === order.Instrument
    );

    if (!instrument) {
      return order;
    }

    const { Product1Symbol, Product2Symbol } = instrument;
    const quantity = currency(order.Quantity, Product1Symbol);
    const origQuantity = currency(order.OrigQuantity, Product1Symbol);
    const price = currency(order.Price, Product2Symbol);

    return {
      ...order,
      quantity,
      OrigQuantity: origQuantity,
      Price: price
    };
  });
}

const tradeReportsCurrecnySelector = createSelector(
  [
    state => state.orderHistory.tradeReports,
    state => state.instrument.instruments
  ],
  (tradeReports, instruments) => {
    const trades = tradeReports.filter(order => order.OrderState !== 'Working');
    trades.map(order => {
      const instrument = instruments.find(
        item => item.InstrumentId === order.InstrumentId || order.Instrument
      );
      const { Product1Symbol, Product2Symbol } = instrument;
      const quantity = currency(order.Quantity, Product1Symbol);
      const price = currency(order.Price, Product2Symbol);
      const symbol = order.Side === 'Buy' ? Product1Symbol : Product2Symbol;
      const fee = currency(order.Fee, symbol);

      return {
        ...order,
        Quantity: quantity,
        Price: price,
        Fee: fee
      };
    });

    return trades;
  }
);

const tradeReportsSelector = createSelector(
  [tradeReportsCurrecnySelector, accountTradesCurrencySelector],
  (tradeReports, accountTrades) => {
    const blockTrades = accountTrades.filter(trade => trade.IsBlockTrade);

    return tradeReports
      .concat(blockTrades)
      .map(order => ({
        ...order,
        SortingTime: order.ReceiveTimeTicks
          ? order.ReceiveTimeTicks
          : order.TradeTime
      }))
      .sort((a, b) => {
        if (a.SortingTime < b.SortingTime) return 1;
        if (a.SortingTime > b.SortingTime) return -1;
        return 0;
      });
  }
);

export const tradeReportsSelectorFactory = () =>
  createSelector(
    [
      tradeReportsSelector,
      state => state.position.selectedProductId,
      state => state.instrument.selectedInstrumentId,
      state => state.instrument.instruments,
      state => formSelector(state, 'startDate', 'endDate'),
      (state, props) => props.filterMode
    ],
    (
      orders,
      selectedProductId,
      selectedInstrumentId,
      instruments,
      formValues,
      filterMode
    ) => {
      if (filterMode === 'selectedProduct') {
        const filteredOrders = filterOrdersByProductId(
          orders,
          selectedProductId,
          instruments
        );
        return filterOrdersByDate(filteredOrders, formValues, 'TradeTimeMS');
      } else {
        // else filterMode == 'selectedInstrument'
        return orders.filter(
          order =>
            order.InstrumentId === selectedInstrumentId ||
            order.Instrument === selectedInstrumentId
        );
      }
    }
  );

function filterOrdersByProductId(orders, selectedProductId, instruments) {
  const ordersWithProductIds = orders.map(order => {
    const instrument = instruments.find(
      item =>
        item.InstrumentId === order.Instrument ||
        item.InstrumentId === order.InstrumentId
    );

    return {
      ...order,
      product1Id: instrument ? instrument.Product1 : null,
      product2Id: instrument ? instrument.Product2 : null
    };
  });

  return ordersWithProductIds.filter(
    order =>
      order.product1Id === selectedProductId ||
      order.product2Id === selectedProductId
  );
}

function filterOrdersByDate(orders, formValues, timeStamp) {
  return orders.filter(item =>
    dateFilter(item, formValues.startDate, formValues.endDate, timeStamp)
  );
}



