import React from 'react';
import PropTypes from 'prop-types';
import APTable from '../common/APTable';
import PaddedDecimal from '../common/PaddedDecimal/PaddedDecimal';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import { formatTime } from '../../helpers/dateHelper';

import './RecentTradesComponent.css';

const baseClass = getBEMClasses('recent-trades');

const RecentTradesComponent = (props, context) => {
  const { recentTrades, decimalPlaces, fetching, showTitle } = props;

  const columns = [
    {
      header: context.t('Price'),
      cell: row => (
        <div
          className={baseClass(
            'table-price',
            row.TakerSide === 0 ? 'buy' : 'sell'
          )}>
          <PaddedDecimal
            value={row.Price}
            bemMod={row.TakerSide === 0 ? 'buy' : 'sell'}
            decimals={decimalPlaces[row.Price.symbol]}
          />
        </div>
      )
    },
    {
      header: context.t('Qty'),
      cell: row => (
        <div className={baseClass('table-qty')}>
          <PaddedDecimal
            value={row.Quantity}
            decimals={decimalPlaces[row.Quantity.symbol]}
          />
        </div>
      )
    },
    {
      header: context.t('Time'),
      cell: row => (
        <div className={baseClass('table-time')}>
          {formatTime(row.Timestamp)}
        </div>
      )
    }
  ];

  return (
    <APTable
      {...{
        title: showTitle && context.t('Recent Trades'),
        columns,
        rows: recentTrades,
        baseClass,
        empty: context.t('No recent trades'),
        fetching
      }}
    />
  );
};

RecentTradesComponent.propTypes = {
  recentTrades: PropTypes.arrayOf(
    PropTypes.shape({
      TradeId: PropTypes.number.isRequired,
      TakerSide: PropTypes.number.isRequired,
      Price: PropTypes.object.isRequired,
      Quantity: PropTypes.object.isRequired,
      Timestamp: PropTypes.number.isRequired
    })
  ),
  showTitle: PropTypes.bool
};

RecentTradesComponent.defaultProps = {
  recentTrades: [],
  showTitle: true
};

RecentTradesComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RecentTradesComponent;



